  //   .classLabel .box {
  //     stroke: #21222c;
  //     stroke-width: 3;
  //     fill: #21222c;
  //     opacity: 1;
  //   }
  //   .classLabel .label {
  //     fill: #f1fa8c;
  //   }
  //   .relation {
  //     stroke: #ff79c6;
  //     stroke-width: 1;
  //   }
  //   #compositionStart, #compositionEnd {
  //     fill: #bd93f9;
  //     stroke: #bd93f9;
