import { useState } from "react"
import RehypeReact from "rehype-react"
import SiteLink from "src/components/Link"
import VideoContainer from "src/components/VideoContainer"
import { PostFrontmatter } from "src/utils/types"
import { React } from "src/utils/view"
import Clip from "../Clip"
import Code from "../Code"

type Props = {
  frontmatter: PostFrontmatter
  htmlAst: any
  timeToRead: number
}

const H2 = ({ children }: any) => {
  const [copied] = useState(false)

  const title = children[0]
  const titleId = title
    .replace(/:+/g, "")
    .replace(/\s+/g, "-")
    .toLowerCase()
  const hash = `#${titleId}`

  const handleClick = async () => {
    const url: URL = new URL(document.location.toString())
    url.hash = hash
    await navigator.clipboard.writeText(url.toString())
    // setCopied(true)
    return true
  }
  return (
    <h2 id={titleId}>
      {title}
      <SiteLink href={`#${titleId}`} noDecoration={true} onClick={handleClick}>
        <Clip copied={copied} />
      </SiteLink>
    </h2>
  )
}

const renderAst = new RehypeReact({
  createElement: React.createElement,
  components: { a: SiteLink, video: VideoContainer, code: Code, h2: H2 },
  Fragment: React.Fragment,
}).Compiler

export default function FullPost({ frontmatter, htmlAst, timeToRead }: Props) {
  return (
    <article>
      <h1>{frontmatter.title}</h1>
      <p>{frontmatter.date}</p>
      <p>{timeToRead} min read</p>
      {frontmatter.draft && <p style={{ color: "green" }}>draft</p>}
      {renderAst(htmlAst)}
      <p>{frontmatter.author}</p>
      {frontmatter.version && <p>v{frontmatter.version}</p>}
    </article>
  )
}
